import { getType } from './util';
const Load = function() {
  const out = {};
  const loadedRes = {};
  out.load = function load({ srcList, percentCb, completeCb }) {
    const state = new State(srcList);
    loadRes(srcList, state);
    startLoading(state, completeCb, percentCb);
  }
  function State(srcList) {
    const length = srcList.length;
    this.length = length;
    this.waitNum = length;
    this.percent = 0;
    this.factPercent = 0;
  }
  function loadRes(srcList = [], state) {
    srcList.forEach((src, idx) => {
      let res = loadedRes[src];
      if (res) {
        updateWaitNum(state);
        return; 
      }
      const type = getType(src);
      switch (type) {
        case 'IMAGE':
          res = creatImg();
          break;
        case 'CSS':
          res = creatLink(src);
          break;
        case 'JS':
            res = creatScript();
            break;
        default:
          res = new Image();
          break;
      }
      res.onload = updateWaitNum(state, res, src);
      if (type !== 'CSS') {
        res.src = src;
      }
    });
  }
  function creatImg() {
    return new Image();
  }
  function creatLink(src) {
    const res = document.createElement('link');
    res.rel = 'stylesheet';
    res.type = 'text/css';
    res.href = src;
    window.document.getElementsByTagName('head')[0].appendChild(res);
    return res;
}
  function creatScript() {
    const res = document.createElement('script');
    window.document.body.appendChild(res);
    return res;
  }
  function updateWaitNum(state, res, src) {
    --state.waitNum;
    if (res) {
      loadedRes[src] = res;
    }
  }
  function startLoading(state, completeCb, percentCb) {
    let percent = 0;
    const loadTimer = setInterval(function() {
      percent = getPercent(state);
      percentCb(percent);
      if (percent === 100) {
        clearInterval(loadTimer);
        completeCb(loadedRes);
      }
    }, 20);
  }
  function getPercent(state) {
    state.factPercent = Math.floor((state.length - state.waitNum) / state.length * 100);
    if (state.percent + 1 < state.factPercent) {
      return state.percent += 1;
    } else {
      state.percent = state.factPercent;
      return state.percent;
    }
  }
  return out;
}
export default Load();